.btn {
  background-image: linear-gradient(
    to right,
    #3e3d3d 0%,
    #868484 51%,
    #d31010 100%
  );
  margin: 10px;
  padding: 15px 45px;
  text-align: center;
  text-transform: uppercase;
  transition: 0.5s;
  background-size: 200% auto;
  color: white;

  border-radius: 10px;
  border: none;
  cursor: pointer;
}

.btn:hover {
  background-position: right center; /* change the direction of the change here */
  color: #fff;
  text-decoration: none;
  box-shadow: 0 0 20px #eee;
}

.container {
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 10px;
}
.text {
  font-size: 20px;
  color: #fff;
}
